<template>
  <div class="content">
    <img src="@/assets/personal/about.png" alt />
    <div class="title">
      <img :src="about.icon" alt />
      <span>{{ about.title }}</span>
    </div>
    <div class="intro">
      <span class="square"></span>
      <span class="about">店铺简介</span>
      <div class="intro-c">{{ about.content }}</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      about: {
        id: 1,
        icon:require('@/assets/personal/yxm.png'),
        title: "关于悦上美",
        content: "&lt;p&gt;这是一句测试的话&lt;/p&gt;"
      }
    };
  }
};
</script>

<style lang="scss" scoped>
.content {
  padding: 20px 24px 0;
  font-size: 28px;
  color: #222;
  img {
    width: 100%;
  }
  .title {
    margin-top: 60px;
    img {
      width: 120px;
      height: 120px;
      vertical-align: middle;
    }
    span {
      margin-left: 14px;
    }
  }
  .intro {
    margin-top: 48px;
    .square {
      display: inline-block;
      width: 12px;
      height: 12px;
      background-color: #333;
      margin-right: 13px;
    }
    .about {
      vertical-align: middle;
    }
    .intro-c {
      padding: 15px 24px 0 30px;
    }
  }
}
</style>



